@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.4.1/font/bootstrap-icons.css);
html {
  margin: 0;
  height: 100%;
  min-height: 100%;
  font-family: sans-serif;
}

body {
  margin: 0;
  padding: 0;
  color: #4f4f4f;
  background-color: #f4f4f4;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  overflow: hidden;
  max-height: calc(100% - 55px);
}

#root {
  overflow: auto;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  margin: 0 10% 20px 10%;
}

h1 {
  font-weight: 300;
  text-transform: capitalize;
  text-align: center;
  /* position: sticky; */
  top: 0;
  background-color: #f4f4f4;
  font-size: 25px;
}

h2 {
  font-weight: 500;
}

.bar {
  position: fixed;
  background-color: white;
  bottom: 0;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  height: 50px;
  box-shadow: 0px -3px 6px 0px rgba(0, 0, 0, 0.2);
  font-size: 20px;
  color: grey;
}

i.active {
  color: purple;
  font-size: 25px;
}

th {
  text-align: left;
}

.cuentas,
.transaccion {
  border: solid 1px #dddddd;
  background-color: white;
  -webkit-flex: 1 1;
          flex: 1 1;
  width: 100%;
  box-shadow: 5px 5px 8px rgb(91 91 91 / 40%);
}

.cuentas {
  border-radius: 5px;
}

.cuentas td,
th {
  padding: 8px 10px;
}

.cuentas td {
  color: #565656;
}

.cuentas tr:nth-child(2n) {
  background-color: #f4f4f4;
}

.saldo {
  font-weight: 600;
}

.transaccion {
  border-bottom: 0;
}

.transaccion .Encabezado,
.Detalle {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 10px;
}

.transaccion .Encabezado {
  background-color: #f4f4f4;
  font-weight: 550;
}

.dia {
  font-size: 12px;
}

.transacion-control {
  width: 70px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.valor {
  font-size: 15px;
  margin-left: 10px;
}

